<template>
  <van-tabbar v-model="activeVlue">
    <van-tabbar-item @click="goto('todo')" icon="idcard" info="10"
      >待处理</van-tabbar-item
    >
    <van-tabbar-item @click="goto('done')" icon="idcard"
      >已处理</van-tabbar-item
    >
    <van-tabbar-item @click="goto('applyfor')" icon="idcard"
      >已申请</van-tabbar-item
    >
    <van-tabbar-item @click="goto('report')" icon="idcard"
      >报表</van-tabbar-item
    >
  </van-tabbar>
</template>

<script>
import { Tabbar, TabbarItem } from "vant";
export default {
  name: "BottomBar",
  components: {
    [Tabbar.name]: Tabbar,
    [TabbarItem.name]: TabbarItem
  },
  data() {
    return {
      activeVlue: this.active || 0
    };
  },
  props: {
    active: Number
  },
  methods: {
    goto(name) {
      this.$router.push({
        name: name
      });
    }
  }
};
</script>

<style lang="scss" scoped></style>
