import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.BASE_URL,
  routes: [
    {
      path: "*",
      redirect: "/login"
    },
    {
      path: "/login",
      name: "login",
      component: () => import("@/views/Login"),
      meta: {
        title: "登录"
      }
    },
    {
      path: "/todo",
      name: "todo",
      component: () => import("@/views/Todo"),
      meta: {
        title: "代办"
      }
    },
    {
      path: "/done",
      name: "done",
      component: () => import("@/views/Done"),
      meta: {
        title: "已办"
      }
    },
    {
      path: "/applyfor",
      name: "applyfor",
      component: () => import("@/views/Applyfor"),
      meta: {
        title: "申请"
      }
    },
    {
      path: "/report",
      name: "report",
      component: () => import("@/views/Report"),
      meta: {
        title: "报表"
      }
    },
    {
      path: "/itemdetail",
      name: "itemdetail",
      component: () => import("@/views/ItemDetail"),
      meta: {
        title: "项目计划详情"
      }
    }
  ]
});
